import { BrowserRouter, Routes, Route } from "react-router-dom";
import Header from "../routes/Header";
import Movie from "../routes/Movie";
import Search from "../routes/Search";
import Tv from "../routes/Tv";
import Home from "../routes/Home";
import Footer from "./Footer";

const HomeRouter = () => {
  return (
    <>
      <BrowserRouter>
        <Header />
        <Routes>
          <Route path="/My_Netflix/" element={<Home />} />
          <Route path="/My_Netflix/movie" element={<Movie />} />
          <Route
            path="/My_Netflix/movie/:category/:movieId"
            element={<Movie />}
          />
          <Route path="/My_Netflix/tv" element={<Tv />} />
          <Route path="/My_Netflix/tv/:category/:movieId" element={<Tv />} />
          <Route path="/My_Netflix/search" element={<Search />} />
          <Route
            path="/My_Netflix/search/:category/:movieId"
            element={<Search />}
          />
          <Route path="/My_Netflix/tv/search2/:movieId" element={<Search />} />
        </Routes>
        <Footer />
      </BrowserRouter>
    </>
  );
};

export default HomeRouter;
